import {goodsList,goodsCount} from "../../util/request"
const state={
    goodsList:[],
    goodsCount:null,
    size:2,
    page:1
}
const mutations={
    changeGoods(state,arr){
        state.goodsList=arr
    },
    changeCount(state,n){
        state.goodsCount=n
    },
    changePage(state,n){
        state.page=n
    }
}
const actions={
    goodsActions(conext){
        //发送请求
        var a={size:conext.state.size,page:conext.state.page}
        goodsList().then(res=>{
            if((res.data.list==null || res.data.list.length==0) && conext.state.page>1){
                conext.commit("changePage",conext.state.page-1);
                conext.dispatch("goodsActions")
                return
            }
            conext.commit("changeGoods",res.data.list)
        })
    },
    countActions(conext){
        goodsCount().then(res=>{
            conext.commit("changeCount",res.data.list[0].total)
        })
    },
    pageActions(conext,n){
        conext.commit("changePage",n)
    }
}
const getters={
    list(state){
        return state.goodsList
    },
    count(state){
        return state.goodsCount
    },
    page(state){
        return state.page
    },
    size(state){
        return state.size
    }
}

export default{
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


