import {memberList} from "../../util/request"
const state={
    memberList:[]
}
const mutations={
    changeMember(state,arr){
        state.memberList=arr
    }
}
const actions={
    memberActions(conext){
        if(state.memberList.length==0){
            memberList().then(res=>{
                conext.commit("changeMember",res.data.list)
            })
        }
    }
}
const getters={
    list(state){
        return state.memberList
    }
}
export default{
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


