import Vue from 'vue'
import Router from 'vue-router'
import store from "../store/index"

Vue.use(Router)

function r(url){
  return store.state.user.menus_url.some(i=>i==url) 
}

const router=new Router({
  routes: [
    {
      path: "/login",
      name:"登录",
      component:()=>import("../pages/login/login.vue")
    },
    {
      path: "*",
      redirect:"/login"
    },
    {
      path: "/",
      redirect:"home",
      component: () => import("../pages/index/index.vue"),

      children: [
        {
          path: "home",
          component:()=>import("../pages/home/home.vue"),
        },
        {
          path: "menu",
          name:"菜单管理",
          component: () => import("../pages/menu/menu.vue"),
          beforeEnter(to,from,next){
            r("menu")?next():next('/')
          }
        },
        {
          path:"role",
          name:"角色管理",
          component:()=>import("../pages/role/role.vue"),
          beforeEnter(to,from,next){
            r("role")?next():next('/')
          }
        },
        {
          path:"manger",
          name:"管理员管理",
          component:()=>import("../pages/manger/manger.vue"),
          beforeEnter(to,from,next){
            r("manger")?next():next('/')
          }
        },
        {
          path:"category",
          name:"商品分类",
          component:()=>import("../pages/category/category.vue"),
          beforeEnter(to,from,next){
            r("category")?next():next('/')
          }
        },
        {
          path:"spec",
          name:"商品规格",
          component:()=>import("../pages/spec/spec.vue"),
          beforeEnter(to,from,next){
            r("spec")?next():next('/')
          }
        },
        {
          path:"goodmanag",
          name:"商品管理",
          component:()=>import("../pages/goodmanag/goodmanag.vue"),
          beforeEnter(to,from,next){
            r("goodmanag")?next():next('/')
          }
        },
        {
          path:"member",
          name:"会员管理",
          component:()=>import("../pages/member/member.vue"),
          beforeEnter(to,from,next){
            r("member")?next():next('/')
          }
        },
        {
          path:"banner",
          name:"轮播图管理",
          component:()=>import("../pages/banner/banner.vue"),
          beforeEnter(to,from,next){
            r("banner")?next():next('/')
          }
        },
        {
          path:"seckill",
          name:"秒杀活动管理",
          component:()=>import("../pages/seckill/seckill.vue"),
          beforeEnter(to,from,next){
            r("seckill")?next():next('/')
          }
        }
        
      ]
    }
  ]
})

//全局路由配置
router.beforeEach((to,from,next)=>{
  if(store.state.user){//token存在
    if(to.path=="/login"){//token存在并且是登录页
      next("/home")//跳转到首页
    }else{//token存在不是登录页
      next()
    }
  }else{
    if(to.path!=="/login"){
      next("/login")
    }else{
      next()
    }
  }
  
})

export default router



