import {skillList} from "../../util/request"
const state={
    skillList:[]
}
const mutations={
    changeSkilllist(state,arr){
        state.skillList=arr
    }
}
const actions={
    cateActions(conext){
        //发起请求
        if(state.skillList.length==0){
            skillList().then(res=>{
                conext.commit("changeSkilllist",res.data.list)
            })
        }
    }
}
const getters={
    getSkill(state){
        return state.skillList
    }
}

export default{
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


