import {specList,countSpec} from "../../util/request"
const state={
    specList:[],
    size:2,
    page:1,
    count:null
}
const mutations={
    changeSpec(state,arr){
        
        state.specList=arr
    },
    changeCount(state,n){
        state.count=n
    },
    changePage(state,n){
        state.page=n
    },
    changeSize(state,n){
        state.size=n
    }
}
const actions={
    specActions(conext){
        specList({page:conext.state.page,size:conext.state.size}).then(res=>{
            if((res.data.list==null || res.data.list.length==0) && conext.state.page>1){
                conext.commit("changePage",conext.state.page-1);
                conext.dispatch("specActions")
                return
            }

            conext.commit("changeSpec",res.data.list)
        })
    },
    countActions(conext){
        //请求总条数
        countSpec().then(res=>{
            conext.commit("changeCount",res.data.list[0].total)
        })
    },
    sizeActions(conext,n){
        conext.commit("changeSize",n)
    },
    pageActions(conext,n){
        conext.commit("changePage",n)
    }
}

const getters={
    list(state){
        return state.specList
    },
    size(state){
        return state.size
    },
    count(state){
        return state.count
    },
    page(state){
        return state.page
    }
}
export default {
    state,
    mutations,
    actions,
    getters,
    namespaced:true
    
}


