//添加分类
export const addCate=(data)=>{
    //上传有文件的请求
    var form=new FormData();
    for (const key in data) {
        form.append(key,data[key])
    }
    console.log(form);
    return axios({
        method:"post",
        url:baseUrl+"/api/cateadd",
        data:form
    })
}

