import { cateList } from "../../util/request"
const state = {
    cateList: []
}
const mutations = {
    changeCateList(state, arr) {
        state.cateList = arr
    }
}
const actions = {
    cateActions(conext) {
        if (state.cateList.length == 0) {
            //发起请求
            cateList({ istree: true }).then(res => {
                conext.commit("changeCateList", res.data.list)
            })
        }

    }
}
const getters = {
    getCate(state) {
        return state.cateList
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
    namespaced: true
}


