import {bannerList} from "../../util/request"
const state={
    bannerList:[]
}
const mutations={
    changeBanner(state,arr){
        state.bannerList=arr
    }
}
const actions={
    bannerActions(conext){
        if(state.bannerList==0){
            bannerList().then(res=>{
                conext.commit("changeBanner",res.data.list)
            })
        }
        
    }
}
const getters={
    list(state){
        return state.bannerList
    }
}
export default {
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


