import {mangerList,mangerCount} from "../../util/request"
const state={
    mangerList:[],
    //页面显示的条数
    size:2,
    //当前显示的页码
    page:1,
    //总条数
    count:null
}
const mutations={
    changeMange(state,arr){
        state.mangerList=arr
    },
    changeCount(state,n){
        state.count=n
    },
    changePage(state,cPage){
        state.page=cPage
    }
}
const actions={
    mangerActions(conext){
        mangerList({size:conext.state.size,page:conext.state.page}).then(res=>{
            if((res.data.list==null || res.data.list.length==0) && conext.state.page>1){
                conext.commit("changePage",conext.state.page-1);
                conext.dispatch("mangerActions")
                return
            }
            conext.commit("changeMange",res.data.list)
        })
    },
    countActions(conext){
        mangerCount().then(res=>{
            conext.commit("changeCount",res.data.list[0].total)
        })
    },
    pageActions(conext,cPage){
        conext.commit("changePage",cPage)
    }
}
const getters={
    getManger(state){
        return  state.mangerList
    },
    getCount(state){
        return state.count
    },
    getPage(state){
        return state.page
    },
    getSize(state){
        return state.size
    }
}
export default{
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


