import {requestMenuList} from "../../util/request"
const state={
    menuList:[]
}
const mutations={
    changeMenuList(state,arr){
        state.menuList=arr
    }
}

const actions={
    menuListActions(conext){
      if(state.menuList.length==0){
        requestMenuList({istree:true}).then(res=>{
            if(res.data.code==200){
                conext.commit("changeMenuList",res.data.list)
            }
        })
      }
    }
}

const getters={
    getmenuList(state){
        return state.menuList
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


