import {roleList} from "../../util/request"
const state={
    roleList:[]
};
const mutations={
    changeRolelist(state,arr){
        state.roleList=arr
    }
};
const actions={
    roleActions(conext){
        //获取列表请求
        if(state.roleList.length==0){
            roleList().then(res=>{
                conext.commit("changeRolelist",res.data.list)
            })
        }
    }
};
const getters={
    getRolelist(state){
        return state.roleList
    }
}
export default {
    state,
    mutations,
    actions,
    getters,
    namespaced:true
}


